import React from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import * as P from "./pages";
import * as C from "./components";

const Routes = () => {
  return (
    <Router>
      <C.Navbar />

      <Switch>
        <Route exact path='/poke-shop' component={P.TypesListPage} />
        <Route path='/poke-shop/lista-tipos' component={P.TypesListPage} />
        <Route path='/poke-shop/pokemon/:id' component={P.PokemonPage} />
        <Route path='/poke-shop/carrinho' component={P.CartPage} />
        <Route path='/poke-shop/:slug' component={P.BasePage} />
      </Switch>
    </Router>
  );
};

export default Routes;
